import ManagerDetails from "./managerDetails-model.js";
const managerDetails = new ManagerDetails();
export default {
	data() {
		return {
			options: {},
			info: {}
		};
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.info = JSON.parse(that.options.data);
			// console.log(that.info)
			// that.wx_login(() => {
			// 	that.getUserInfo(() => {
			// 		callBack && callBack();
			// 	})
			// })
		},
		// 关注
		attention(callBack) {
			const that = this
			managerDetails.runAttention({
				openid: that.userInfo.openid,
				id: that.info.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					managerDetails.show_tips(res.explain)
				}
				callBack && callBack();
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return index.onShareAppMessage(shareData);
	}
}
